<template>
	<div id="activity-bargainGoods-search">
		<public-header title="创建商品活动"></public-header>
		<div class="content">
			<van-search v-model="keyword"	placeholder="请输入商品名称"	class="padding-10-tb"/>
			<van-row class="content-inner">
				<van-col span="24" class="wh-100 goods-wrap">
					<div class="goods-list padding-15-lr bg-default" v-if="goodsList.length > 0">
						<div class="goods-item van-hairline--bottom"
							 v-for="(item,index) in goodsList"
						>
							<div @click="onToggleCheckBox(index)">
								<van-row >
									<van-col span="5">
										<img class="goods-thumb" :src="item.thumb" alt="">
									</van-col>
									<van-col span="17">
										<div class="w-100 ellipsis font-bold font-14">{{item.title}}</div>
										<div class="margin-5-t c-gray font-12">
											<span class="padding-10-r">月售{{item.sailed}}</span>
											<span>好评{{item.per_comment_good}}%</span>
										</div>
										<div class="discount flex margin-5-t hide" v-if="item.is_bargain">
											<span class="mark flex" >
												<span>¥{{item.discount_price}}</span>
												<span>折扣</span>
											</span>
											<span class="c-gray font-12 margin-5-l">351天后到期</span>
										</div>
										<div class="margin-5-t">
											<span>¥{{item.price}}</span>
											<span class="padding-10-l c-gray font-12 hide" >已参与其他活动</span>
										</div>
									</van-col>
									<van-col span="2" class="text-right">
										<div class="radio-box icon" :class="{'icon-check': item.is_bargain}"></div>
									</van-col>
								</van-row>
							</div>
							<div class="goods-set" v-if="item.is_bargain">
								<div class="flex-lr font-12 margin-5-b">
									<span>活动价</span>
									<div class="input-unit" >
										<input type="text" v-model.number="item.discount_price">
										<span class="font-12 bg-default">元</span>
									</div>
								</div>
								<div class="flex-lr font-12 margin-5-b">
									<span>每单限购</span>
									<div class="input-unit" >
										<input type="text" v-model.number="item.max_buy_limit">
										<span class="font-12 bg-default">元</span>
									</div>
								</div>
								<div class="flex-lr font-12 margin-5-b">
									<span>每日库存</span>
									<div class="input-unit" >
										<input type="text" v-model.number="item.discount_total">
										<span class="font-12 bg-default">份</span>
									</div>
								</div>
								<div class="flex-lr font-12 margin-5-b">
									<span>当日剩余库存</span>
									<div class="input-unit" >
										<input type="text" v-model.number="item.discount_available_total">
										<span class="font-12 bg-default">份</span>
									</div>
								</div>
								<div class="flex-lr font-12">
									<span>新用户专享</span>
									<iswitch :value="item.poi_user_type" text-open="ON" text-close="OFF" condition-open="new" condition-close="all" @change="jsToggleSwitch" :keys="`goodsList.${index}.poi_user_type`"></iswitch>
								</div>
							</div>
						</div>
					</div>
				</van-col>
			</van-row>
			<div class="fix-bottom van-hairline--top">
				<van-button size="normal" block class="bg-info font-16" @click="onSubmit">选好了, 返回商品列表</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData} from "@/controller/funcCommon"
	import {mapState, mapMutations} from 'vuex'
	export default {
		components: {
			publicHeader,
			iswitch
		},
		data() {
			return {
				keyword: '',
				goodsList: [],
				showPreLoading: true
			}
		},
		watch: {
			keyword: function() {
				this.onSearch();
			}
		},
		computed: {
			...mapState([
				'bargainGoods'
			]),
		},
		methods: {
			...mapMutations([
				'setBargainGoods'
			]),
			onToggleCheckBox(index) {
				var that = this;
				that.goodsList[index].is_bargain = !that.goodsList[index].is_bargain;
				if(!that.goodsList[index].is_bargain && that.bargainGoods.hasOwnProperty(that.goodsList[index].id)) {
					delete that.bargainGoods[that.goodsList[index].id];
				}
			},
			jsToggleSwitch(res) {
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			},
			onSearch() {
				var that = this;
				if(!that.keyword) {
					return;
				}
				getData({
					vue: that,
					url: 'manage/activity/bargain/search',
					data: {
						keyword: that.keyword,
					},
					success: (res) => {
						let goodsList = res.goods;
						if(that.util.length(that.bargainGoods) > 0) {
							if(goodsList && goodsList.length > 0) {
								for(let x in goodsList) {
									if(that.bargainGoods.hasOwnProperty(goodsList[x].id)) {
										goodsList[x] = that.bargainGoods[goodsList[x].id];
									} else {
										continue;
									}
								}
							}
						}
						that.goodsList = [...goodsList];
					}
				})
			},
			onSubmit() {
				var that = this;
				var goodsList = that.goodsList;
				var temp = {};
				if(goodsList.length > 0) {
					for(let x in goodsList) {
						temp = goodsList[x];
						if(temp.is_bargain) {
							that.bargainGoods[temp.id] = temp;
						} else {
							continue;
						}
					}
				}
				that.setBargainGoods(that.bargainGoods);
				that.$router.go(-1);
			}
		},
		mounted() {
			this.showPreLoading = false;
		}
	}
</script>

<style>
	#activity-bargainGoods-search .van-search__cancel{
		color: #7a7a7a;
	}
	#activity-bargainGoods-search .fix-bottom{
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		padding: 10px 15px;
		background-color: #fff;
	}
	#activity-bargainGoods-search .content-inner{
		position: absolute;
		top: 50px;
		bottom: 65px;
		left: 0;
		right: 0;
	}
	#activity-bargainGoods-search  .goods-wrap{
		position: relative;
	}
	#activity-bargainGoods-search .goods-list{
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#activity-bargainGoods-search .goods-list .goods-item{
		padding: 15px 0;
	}
	#activity-bargainGoods-search .goods-list .goods-item:last-child:after{
		border: 0px;
	}
	#activity-bargainGoods-search .goods-item .goods-thumb{
		width: 50px;
		height: 50px;
		border-radius: 5px;
		overflow: hidden;
	}
	#activity-bargainGoods-search .goods-item .van-radio{
		margin-top: 20px;
	}
	#activity-bargainGoods-search .goods-item .discount .mark{
		border-radius: 3px;
		background-color: #ff2c4c;
		font-size: 11px;
		border: 1px solid #ff2c4c;
		overflow: hidden;
	}
	#activity-bargainGoods-search .goods-item .discount .mark span:first-child{
		padding: 2px 5px;
		color: #fff;
	}
	#activity-bargainGoods-search .goods-item .discount .mark span:last-child{
		padding: 2px 5px;
		color: #ff2c4c;
		background-color: #fff;
	}
	#activity-bargainGoods-search .goods-item .radio-box{
		margin-top: 20px;
	}
	#activity-bargainGoods-search .goods-item .goods-set{
		width: 70%;
		padding: 10px;
		background-color: #f5f5f5;
		margin: 10px 10% 0 20%;
	}
	#activity-bargainGoods-search .goods-item .radio-box{
		margin-top: 20px;
		width: 20px;
		height: 20px;
		border-radius: 50%;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		border: 1px solid #aaa;
		color: #fff;
	}
	#activity-bargainGoods-search .goods-item .radio-box.icon-check{
		background-color: #33aafc;
		border-color: #33aafc;
	}
	#activity-bargainGoods-search .goods-item .iswitch{
		height: 22px;
		min-width: 73px;
	}
</style>


